import React, { useState, useEffect } from 'react'
import Axios from 'axios'
import { cleanAndCapName, getUrlId } from '../../scripts/helpers'

export default function CategorySelect({ category = 'pokemon', setCategory }) {
	const searchParams = new URLSearchParams(window.location.href)

	// const searchParams = new URLSearchParams(window.location.href)
	// const paramCategory = searchParams.get('category')
	// if (paramCategory && paramCategory !== category) setCategory(paramCategory)

	const paramCategory = searchParams.get('category')

	useEffect(() => {
		//console.log('CategorySelect useEffect')
		if (paramCategory) setCategory(paramCategory)

		//return () => console.log('CategorySelect return')
	}, [])

	return (
		<div className="input-group">
			<div className="input-group-prepend">
				<span className="input-group-text">Category:</span>
			</div>
			<select
				value={category}
				className="form-select"
				onChange={(event) => setCategory(event.target.value)}
			>
				<option value="pokemon">Pokemon</option>
				<option value="ability">Abilities</option>
				<option value="type">Types</option>
				<option value="move">Moves</option>
				{/* <option value="egg-group">Egg Groups</option>
				<option value="growth-rate">Growth Rate</option>
				<option value="pokemon-color">Pokemon Color</option> */}
				<option value="generation">Generation</option>
				<option value="berry">Berry</option>
				<option value="item">Item</option>
			</select>
		</div>
	)
}

export function FilterSelect({ category, setFilteredItems }) {
	const searchParams = new URLSearchParams(window.location.href)

	const [filterCategory, setFilterCategory] = useState('')
	const [filter, setFilter] = useState('')
	const [filterItems, setFilterItems] = useState([])

	let isMounted = false

	useEffect(() => {
		isMounted = true
		const paramFilterCategory = searchParams.get('filterCategory')
		const paramFilter = searchParams.get('filter')
		//console.log('FilterSelect useEffect')
		//console.log(`paramFilterCategory = ${paramFilterCategory}`)
		//console.log(`paramFilter = ${paramFilter}`)

		if (
			!paramFilterCategory ||
			paramFilterCategory.length < 1 ||
			paramFilterCategory === 'none'
		) {
			setFilterCategory(paramFilterCategory ? paramFilterCategory : '')
			setFilter('')
			setFilterItems([])
		} else if (!getFilterCategories().includes(paramFilterCategory)) {
			setFilterCategory('')
			setFilter('')
			setFilterItems([])
			setFilteredItems([])
		} else {
			const filterUrl = `${process.env.REACT_APP_POKE_API}/${paramFilterCategory}?limit=9999`

			Axios.get(filterUrl)
				.then((filterRes) => {
					//console.log('GetFilters')
					const filterItems = filterRes.data.results
					if (filterItems.length > 50)
						filterItems.sort((a, b) => a.name.localeCompare(b.name))

					if (isMounted) {
						setFilterItems(filterItems)
						setFilter(paramFilter ? paramFilter : '')
					}

					let index = ''
					filterItems.some((item) => {
						if (item.name === paramFilter) {
							index = getUrlId(item.url)
							return
						}
					})
					if (index.length < 1) return

					const filterUrl = `${process.env.REACT_APP_POKE_API}/${paramFilterCategory}/${index}`
					Axios.get(filterUrl)
						.then((filterRes) => {
							let filteredItems = []

							if (category === 'pokemon') {
								switch (paramFilterCategory) {
									case 'type':
										filteredItems =
											filterRes.data.pokemon.map(
												(pokemon) => {
													return pokemon.pokemon
												}
											)
										break
									// case 'move':
									// 	filteredItems = filterRes.learned_by_pokemon
									// 	break
									case 'ability':
										filteredItems =
											filterRes.data.pokemon.map(
												(pokemon) => {
													return pokemon.pokemon
												}
											)
										break
									case 'pokedex':
										filteredItems =
											filterRes.data.pokemon_entries.map(
												(entry) => {
													return entry.pokemon_species
												}
											)
										break
									case 'generation':
									case 'egg-group':
									case 'growth-rate':
									case 'pokemon-shape':
									case 'pokemon-color':
									case 'pokemon-habitat':
										filteredItems =
											filterRes.data.pokemon_species
										break
								}
							} else if (category === 'move') {
								filteredItems = filterRes.data.moves
							} else if (category === 'berry') {
								switch (paramFilterCategory) {
									case 'berry-flavor':
										filteredItems = filterRes.data.berries
											.map((berry) => {
												return berry.berry
											})
											.reverse()
										break
									case 'berry-firmness':
										filteredItems = filterRes.data.berries
										break
								}
							} else if (category === 'item') {
								filteredItems = filterRes.data.items
							}

							if (isMounted) {
								setFilteredItems(
									filteredItems ? filteredItems : []
								)
							}
						})
						.catch((err) => {
							console.log(err)
						})
				})
				.catch((err) => {
					//console.log('GetFilters Error')
					console.log(err)
				})

			if (isMounted) {
				setFilterCategory(
					paramFilterCategory ? paramFilterCategory : ''
				)
			}
		}

		setFilter(paramFilter ? paramFilter : '')

		return () => {
			isMounted = false
			//console.log('FilterSelect return')
		}
	}, [category])

	function getFilterCategories() {
		switch (category) {
			case 'pokemon':
				return [
					'type',
					// 'move',
					'ability',
					'generation',
					'egg-group',
					'growth-rate',
					'pokedex',
					'pokemon-shape',
					'pokemon-habitat',
					'pokemon-color',
				]
			case 'move':
				return [
					'type',
					'generation',
					'move-ailment',
					'move-category',
					'move-damage-class',
					'move-target',
				]
			case 'berry':
				return ['berry-flavor', 'berry-firmness']
			case 'item':
				return ['item-attribute', 'item-category']
			default:
				return []
		}
	}

	function handleFilterCategory(event) {
		const filterCategory = event.target.value

		if (isMounted) {
			setFilter([])
			setFilteredItems([])
		}

		//console.log('filterCategory = ' + filterCategory)
		if (filterCategory.length < 1 || filterCategory === 'none') {
			if (isMounted) setFilterItems([])
		} else {
			const filterUrl = `${process.env.REACT_APP_POKE_API}/${filterCategory}?limit=9999`
			Axios.get(filterUrl).then((filterRes) => {
				const filterItems = filterRes.data.results
				if (filterItems.length > 50)
					filterItems.sort((a, b) => a.name.localeCompare(b.name))

				if (isMounted) {
					setFilterItems(filterItems)
					setFilter(filter ? filter : '')
				}
			})
		}

		if (isMounted) setFilterCategory(filterCategory)
	}

	function handleFilter(event) {
		const filter = event.target.value
		if (filter.length < 1 || filter === 'none') {
			if (isMounted) {
				setFilter(filter)
				setFilteredItems([])
			}
			return
		}

		let index = ''
		filterItems.some((item) => {
			if (item.name === filter) {
				index = getUrlId(item.url)
				return
			}
		})

		const filterUrl = `${process.env.REACT_APP_POKE_API}/${filterCategory}/${index}`
		Axios.get(filterUrl).then((filterRes) => {
			let filteredItems = []

			if (category === 'pokemon') {
				switch (filterCategory) {
					case 'type':
						filteredItems = filterRes.data.pokemon.map(
							(pokemon) => {
								return pokemon.pokemon
							}
						)
						break
					// case 'move':
					// 	filteredItems = filterRes.learned_by_pokemon
					// 	break
					case 'ability':
						filteredItems = filterRes.data.pokemon.map(
							(pokemon) => {
								return pokemon.pokemon
							}
						)
						break
					case 'pokedex':
						filteredItems = filterRes.data.pokemon_entries.map(
							(entry) => {
								return entry.pokemon_species
							}
						)
						break
					case 'generation':
					case 'egg-group':
					case 'growth-rate':
					case 'pokemon-shape':
					case 'pokemon-color':
					case 'pokemon-habitat':
						filteredItems = filterRes.data.pokemon_species
						break
				}
			} else if (category === 'move') {
				filteredItems = filterRes.data.moves
			} else if (category === 'berry') {
				switch (filterCategory) {
					case 'berry-flavor':
						filteredItems = filterRes.data.berries
							.map((berry) => {
								return berry.berry
							})
							.reverse()
						break
					case 'berry-firmness':
						filteredItems = filterRes.data.berries
						break
				}
			} else if (category === 'item') {
				filteredItems = filterRes.data.items
			}

			if (isMounted) setFilteredItems(filteredItems ? filteredItems : [])
		})

		if (isMounted) setFilter(filter)
	}

	return (
		<div className="input-group">
			<div className="input-group-prepend">
				<span className="input-group-text">Filter:</span>
			</div>
			<select
				value={filterCategory}
				className="form-select"
				onChange={handleFilterCategory}
			>
				<option value="none">None</option>
				{getFilterCategories().map((name) => (
					<option key={name} value={name}>
						{cleanAndCapName(name)}
					</option>
				))}
			</select>
			<select
				value={filter}
				className="form-select"
				onChange={handleFilter}
			>
				<option value="none">None</option>
				{filterItems &&
					filterItems.map((item) => (
						<option key={item.name} value={item.name}>
							{cleanAndCapName(item.name)}
							{/* console.log(Math.floor(Date.now() / 1000) + " " + item.name) */}
						</option>
					))}
			</select>
		</div>
	)
}
